@import "../../../css-config/mixins.scss";

.search-modal {
  position: fixed;
  width: 100%;
  height: 100%;
  
  z-index: 999;
  top: 0;
  left: 0;
  background-color: white;
  transform: translateY(100%);
  animation: slidesearch 0.5s ease forwards ;
  .form-field {
    width: 100%;
    display: flex;
    justify-content: center;
    padding: 10px 50px;
    // border-bottom: 1 px solid rgba(157, 143, 143, 0.1)!important;
    @include md {
      padding: 20px 0;
    }
    input {
      width: 100%;
      max-width: 1200px;
      text-align: center;
      font-size: 20px;
      font-weight: 600;
      border-width: 0px;
      outline:0; /* I have also tried outline:none */
        box-shadow: none;
      -moz-box-shadow: none;
      -webkit-box-shadow: none;
    //   outline: none!important;   
    outline-color: rgba(157, 143, 143, 0.1);
      bottom: none;
      text-transform: uppercase;
      @include md {
        font-size: 40px;
        height: 80px;
      }
    }
    .close-btn{
        position: absolute;
        font-size: 24px;
        right: 20px;
        top: 50px;
        transform: translateY(-50%);
        
        cursor: pointer;
        // @include md {
        //     right: 40px;
            
        //     font-size: 50px;
        // }
        @include ssm{
          top : 20px;
        }
        
    }
  }
  .search-result-content{
    
     min-width: calc(100% - 20px);
     margin: 0 auto;
     @include md {
      max-width: 800px;

     }
    .search-results{
       height: calc(100vh - 110px);
       overflow: auto;
       margin: 20px 0;
       @include md {
        height: calc(100vh - 160px);

       }
        .search-result-item{
            display: flex;
            align-items: center;
            gap: 10px;
            padding: 10px 0;
            border-bottom: 1px solid rgba(0,0,0,0.1);
            cursor: pointer;
            .image-container{
              background-color: rgba(0,0,0,0.05);
                          width: 60px;
                          height: 60px;
                          flex-shrink: 0;
                          img{
                              width: 100%;
                              height: 100%;
                          }
            }
            .prod-details{
              overflow: hidden;
              .name{
                text-overflow: ellipsis;
                white-space: nowrap;
                overflow: hidden;
                font-size: 16px;
                line-height: 1;
                margin-bottom: 10px;
                font-weight: 600;
                display: block;

              }
              .desc{
                font-size: 14px;
                line-height: 1;
                color: rgba(0,0,0,0.5);


              }
            }

        }
    }
  }
}

